.SortVisualizer {
  width: auto;
  padding: 0.5rem;
  padding-top: 1rem;
  margin: 1rem 1rem;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14);
  background-color: rgb(58, 58, 60);
  transition: 0.5s ease-in-out;
}

.ProgressBarOut {
  width: 100%;
  overflow: clip;
  margin-top: 0.5rem;
}
